/* =============
   Charts
============= */
/* Flot chart */
#flotTip {
  padding: 8px 12px;
  background-color: #ffffff;
  z-index: 100;
  color: #313a46;
  opacity: 1;
  box-shadow: 0 5px 25px 5px rgba(0, 0, 0, 0.14);
  border-radius: 3px;
}

.legend tr {
  height: 30px;
  font-family: "Rubik", sans-serif;
}

.legendLabel {
  padding-left: 5px !important;
  line-height: 10px;
  padding-right: 20px;
  font-size: 13px;
  font-weight: 500;
  color: rgba(49, 58, 70, 0.6);
  text-transform: uppercase;
}

.legendColorBox div div {
  border-radius: 50%;
}

/* Morris chart */
.morris-hover.morris-default-style {
  border-radius: 3px;
  padding: 10px 12px;
  background: #ffffff;
  border: none;
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  color: #313a46;
}

/* Chartist chart */
.ct-golden-section:before {
  float: none;
}

.ct-chart {
  max-height: 350px;
}

.ct-chart .ct-label {
  fill: #98a6ad;
  color: #98a6ad;
  font-size: 13px;
  line-height: 1;
}

.ct-grid {
  stroke: rgba(49, 58, 70, 0.1);
}

.ct-chart.simple-pie-chart-chartist .ct-label {
  color: #ffffff;
  fill: #ffffff;
  font-size: 16px;
}

.ct-chart .ct-series.ct-series-a .ct-bar, .ct-chart .ct-series.ct-series-a .ct-line, .ct-chart .ct-series.ct-series-a .ct-point, .ct-chart .ct-series.ct-series-a .ct-slice-donut {
  stroke: #02c0ce;
}

.ct-chart .ct-series.ct-series-b .ct-bar, .ct-chart .ct-series.ct-series-b .ct-line, .ct-chart .ct-series.ct-series-b .ct-point, .ct-chart .ct-series.ct-series-b .ct-slice-donut {
  stroke: #e3eaef;
}

.ct-chart .ct-series.ct-series-c .ct-bar, .ct-chart .ct-series.ct-series-c .ct-line, .ct-chart .ct-series.ct-series-c .ct-point, .ct-chart .ct-series.ct-series-c .ct-slice-donut {
  stroke: #2d7bf4;
}

.ct-chart .ct-series.ct-series-d .ct-bar, .ct-chart .ct-series.ct-series-d .ct-line, .ct-chart .ct-series.ct-series-d .ct-point, .ct-chart .ct-series.ct-series-d .ct-slice-donut {
  stroke: #f1556c;
}

.ct-chart .ct-series.ct-series-e .ct-bar, .ct-chart .ct-series.ct-series-e .ct-line, .ct-chart .ct-series.ct-series-e .ct-point, .ct-chart .ct-series.ct-series-e .ct-slice-donut {
  stroke: #4eb7eb;
}

.ct-chart .ct-series.ct-series-f .ct-bar, .ct-chart .ct-series.ct-series-f .ct-line, .ct-chart .ct-series.ct-series-f .ct-point, .ct-chart .ct-series.ct-series-f .ct-slice-donut {
  stroke: #ff679b;
}

.ct-chart .ct-series.ct-series-g .ct-bar, .ct-chart .ct-series.ct-series-g .ct-line, .ct-chart .ct-series.ct-series-g .ct-point, .ct-chart .ct-series.ct-series-g .ct-slice-donut {
  stroke: #f9bc0b;
}

.ct-series-a .ct-area, .ct-series-a .ct-slice-pie {
  fill: #02c0ce;
}

.ct-series-b .ct-area, .ct-series-b .ct-slice-pie {
  fill: #e3eaef;
}

.ct-series-c .ct-area, .ct-series-c .ct-slice-pie {
  fill: #2d7bf4;
}

.ct-series-d .ct-area, .ct-series-d .ct-slice-pie {
  fill: #f1556c;
}

.chartist-tooltip {
  position: absolute;
  display: inline-block;
  opacity: 0;
  min-width: 10px;
  padding: 2px 10px;
  border-radius: 3px;
  background: #313a46;
  color: #ffffff;
  text-align: center;
  pointer-events: none;
  z-index: 1;
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}

.chartist-tooltip.tooltip-show {
  opacity: 1;
}

/* Sparkline chart */
.jqstooltip {
  box-sizing: content-box;
  width: auto !important;
  height: auto !important;
  background-color: #ffffff !important;
  box-shadow: 0 5px 25px 5px rgba(0, 0, 0, 0.14);
  padding: 5px 10px !important;
  border-radius: 3px;
  -moz-border-radius: 3px;
  border-color: #ffffff !important;
}

.jqsfield {
  color: #313a46 !important;
  font-size: 12px !important;
  line-height: 18px !important;
}

/* =============
   Email
============= */
.inbox-leftbar {
  width: 240px;
  float: left;
  padding: 0 20px 20px 10px;
}

.inbox-rightbar {
  margin-left: 250px;
}

.message-list {
  display: block;
  padding-left: 0;
}

.message-list li {
  position: relative;
  display: block;
  height: 50px;
  line-height: 50px;
  cursor: default;
  -webkit-transition-duration: .3s;
          transition-duration: .3s;
}

.message-list li a {
  color: #797979;
}

.message-list li:hover {
  background: rgba(152, 166, 173, 0.15);
  -webkit-transition-duration: .05s;
          transition-duration: .05s;
}

.message-list li .col-mail {
  float: left;
  position: relative;
}

.message-list li .col-mail-1 {
  width: 320px;
}

.message-list li .col-mail-1 .star-toggle,
.message-list li .col-mail-1 .checkbox-wrapper-mail,
.message-list li .col-mail-1 .dot {
  display: block;
  float: left;
}

.message-list li .col-mail-1 .dot {
  border: 4px solid transparent;
  border-radius: 100px;
  margin: 22px 26px 0;
  height: 0;
  width: 0;
  line-height: 0;
  font-size: 0;
}

.message-list li .col-mail-1 .checkbox-wrapper-mail {
  margin: 15px 10px 0 20px;
}

.message-list li .col-mail-1 .star-toggle {
  margin-top: 18px;
  font-size: 16px;
  margin-left: 5px;
}

.message-list li .col-mail-1 .title {
  position: absolute;
  top: 15px;
  left: 110px;
  right: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.message-list li .col-mail-2 {
  position: absolute;
  top: 0;
  left: 320px;
  right: 0;
  bottom: 0;
}

.message-list li .col-mail-2 .subject,
.message-list li .col-mail-2 .date {
  position: absolute;
  top: 0;
}

.message-list li .col-mail-2 .subject {
  left: 0;
  right: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.message-list li .col-mail-2 .date {
  right: 0;
  width: 170px;
  padding-left: 80px;
}

.message-list li.active, .message-list li.selected {
  background: rgba(152, 166, 173, 0.15);
  -webkit-transition-duration: .05s;
          transition-duration: .05s;
}

.message-list li.active,
.message-list li.active:hover {
  box-shadow: inset 3px 0 0 #02c0ce;
}

.message-list li.unread a {
  font-weight: 600;
  color: #272e37;
}

.message-list li.blue-dot .col-mail-1 .dot {
  border-color: #2d7bf4;
}

.message-list li.orange-dot .col-mail-1 .dot {
  border-color: #f9bc0b;
}

.message-list li.green-dot .col-mail-1 .dot {
  border-color: #0acf97;
}

.message-list .checkbox-wrapper-mail {
  cursor: pointer;
  height: 20px;
  width: 20px;
  position: relative;
  display: inline-block;
  box-shadow: inset 0 0 0 1px #98a6ad;
  border-radius: 1px;
}

.message-list .checkbox-wrapper-mail input {
  opacity: 0;
  cursor: pointer;
}

.message-list .checkbox-wrapper-mail input:checked ~ label {
  opacity: 1;
}

.message-list .checkbox-wrapper-mail label {
  position: absolute;
  top: 3px;
  left: 3px;
  right: 3px;
  bottom: 3px;
  cursor: pointer;
  background: #98a6ad;
  opacity: 0;
  margin-bottom: 0 !important;
  -webkit-transition-duration: .05s;
          transition-duration: .05s;
}

.message-list .checkbox-wrapper-mail label:active {
  background: #87949b;
}

.mail-list a {
  font-family: "Rubik", sans-serif;
  vertical-align: middle;
  color: #797979;
  padding: 10px 15px;
  display: block;
}

.reply-box {
  border: 2px solid #e3eaef;
}

@media (max-width: 648px) {
  .inbox-leftbar {
    width: 100%;
  }
  .inbox-rightbar {
    margin-left: 0;
  }
}

@media (max-width: 520px) {
  .message-list li .col-mail-1 {
    width: 150px;
  }
  .message-list li .col-mail-1 .title {
    left: 80px;
  }
  .message-list li .col-mail-2 {
    left: 160px;
  }
  .message-list li .col-mail-2 .date {
    text-align: right;
    padding-right: 10px;
    padding-left: 20px;
  }
}

/* =============
   Form
============= */
label {
  font-size: 14px;
  color: #313a46;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
}

select[multiple] option + option, select[size] option + option {
  margin-top: 1px;
}

select[multiple] option, select[size] option {
  padding: 6px 12px;
  border-radius: 2px;
}

select[multiple] {
  height: auto;
}

textarea.form-control {
  min-height: 90px;
}

.form-control {
  border: 1px solid #d9e3e9;
  border-radius: 4px;
  max-width: 100%;
  padding: .469rem .75rem;
  font-size: 14px;
  box-shadow: none;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
}

.form-control:focus {
  border: 1px solid #a2b9ca;
  box-shadow: none;
  outline: 0;
}

.form-control-lg {
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 4px;
}

.form-control-sm {
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 3px;
}

input[type="color"] {
  min-height: 38px;
}

.input-group-prepend .btn, .input-group-append .btn {
  min-height: 38px;
}

.form-control-sm, .input-group-sm > .form-control, .input-group-sm > .input-group-append > .btn,
.input-group-sm > .input-group-append > .input-group-text, .input-group-sm > .input-group-prepend > .btn,
.input-group-sm > .input-group-prepend > .input-group-text {
  font-size: 12px;
}

.custom-select:focus {
  box-shadow: none;
}

.custom-control-input {
  display: none;
}

.custom-control-input:checked ~ .custom-control-indicator {
  background-color: #02c0ce;
}

.form-check-input:only-child {
  position: absolute;
}

/* =============
   Form Advanced
============= */
/* Select 2 */
.select2-container {
  width: 100% !important;
}

.select2-container .select2-selection--single {
  border: 1px solid #e0e8ed;
  height: 38px;
}

.select2-container .select2-selection--single .select2-selection__rendered {
  line-height: 36px;
  padding-left: 12px;
}

.select2-container .select2-selection--single .select2-selection__arrow {
  height: 34px;
  width: 34px;
  right: 3px;
}

.select2-container .select2-selection--single .select2-selection__arrow b {
  border-color: #b2c5d3 transparent transparent transparent;
  border-width: 6px 6px 0 6px;
}

.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border-color: transparent transparent #b2c5d3 transparent !important;
  border-width: 0 6px 6px 6px !important;
}

.select2-results__option {
  padding: 6px 12px;
}

.select2-dropdown {
  border: 1px solid #d3dee6;
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
}

.select2-container--default .select2-search--dropdown {
  padding: 10px;
  background-color: #f3f6f8;
}

.select2-container--default .select2-search--dropdown .select2-search__field {
  border: 1px solid #d3dee6;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: #02c0ce;
}

.select2-container--default .select2-results__option[aria-selected=true] {
  background-color: #e3eaef;
  color: #313a46;
}

.select2-container--default .select2-results__option[aria-selected=true]:hover {
  background-color: #02c0ce;
  color: #ffffff;
}

.select2-container .select2-selection--multiple {
  min-height: 38px;
  border: 1px solid #d3dee6 !important;
}

.select2-container .select2-selection--multiple .select2-selection__rendered {
  padding: 2px 10px;
}

.select2-container .select2-selection--multiple .select2-search__field {
  margin-top: 7px;
  border: 0;
}

.select2-container .select2-selection--multiple .select2-selection__choice {
  background-color: #02c0ce;
  border: 1px solid transparent;
  color: #ffffff;
  border-radius: 3px;
  padding: 0 7px;
}

.select2-container .select2-selection--multiple .select2-selection__choice__remove {
  color: #ffffff;
  margin-right: 5px;
}

.select2-container .select2-selection--multiple .select2-selection__choice__remove:hover {
  color: #ffffff;
}

/* AUTOCOMPLETE */
.autocomplete-suggestions {
  border: 1px solid #e3eaef;
  background: #ffffff;
  cursor: default;
  overflow: auto;
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
}

.autocomplete-suggestion {
  padding: 5px 8px;
  white-space: nowrap;
  overflow: hidden;
}

.autocomplete-no-suggestion {
  padding: 5px 10px;
  color: #f1556c;
}

.autocomplete-selected {
  background-color: #02c0ce;
  color: #ffffff;
  cursor: pointer;
}

.autocomplete-selected strong {
  color: #ffffff !important;
}

